		-webkit-transform: none;
		transform: none;
	}

	.ui.inline.loader.active,
	.ui.inline.loader.visible {
		display: inline-block;
	}

	/* Centered Inline */
	.ui.centered.inline.loader.active,
	.ui.centered.inline.loader.visible {
		display: block;
		margin-left: auto;
		margin-right: auto;
	}
}